import React from "react";

const defaultImage =
  "data:image/png;base64,";

const TopicData = ({
  name,
  description,
  ownerName,
  ownerEmail,
  qrCode,
  photo,
  datePosted
}) => {
  return (
    <React.Fragment>
      <div>
        <span className="topic-main-property">{name}</span>

        {photo ? (
          <img src={photo} className="topic-main-image" alt="image" />
        ) : (
          <img src={defaultImage} className="topic-main-image" alt="image" />
        )}

        <span className="topic-secondary-property">{description}</span>

        <span className="topic-secondary-property">
          Posted on
          <span className="topic-date">
            {new Date(datePosted).toLocaleDateString()}
          </span>{" "}
          at{" "}
          <span className="topic-date">
            {new Date(datePosted).toLocaleTimeString()}
          </span>{" "}
          by <span className="topic-owner">{ownerName}</span>
        </span>
      </div>
      <a download={"reviewme_" + name} href={qrCode}>
        <button className="btn btn-light download-qrcode-button">
          <span> Download QR Code </span>
        </button>
      </a>
    </React.Fragment>
  );
};

export default TopicData;
